@import "./libs/variables.scss";

/* reset */

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* general styles */
body {
    padding: 3.5% 4%;
}

h3 {
    font-size: 20px;
}

h2 {
    font-style: 30px;
}

.more {
    color: rgb(255, 200, 0);
    font-weight: 500;
}

@import './libs/main.scss';